import React, { Component } from 'react'
import StarIconActive from '../../assets/favorite-3.png'
import ButtonIcon from '../../assets/btn.png'
import {getProgressStyle} from '../../utils/getProgressStyle'
import './index.css'

export default class Topbar extends Component {
  render() {
    const {person: {name, percentage}} = this.props
    const {icon, style} = getProgressStyle(percentage)
    return (
      <div className="stat-item__container">
        <div className="stat-item__name-section">
          <div className="stat-item__name">{name}</div>
          <div className="stat-item__star-container">
            <img src={StarIconActive} alt="star" className="stat-item__star" />
            <img src={StarIconActive} alt="star" className="stat-item__star" />
            <img src={StarIconActive} alt="star" className="stat-item__star" />
          </div>
        </div>
        <div className="stat-item__progress-bar-container">
          <div className="stat-item__progress-bar" style={{width: `${percentage}%`}}>
            <img src={icon} alt="minion" className="stat-item__minion-icon" style={{right: style.right}} />
            <div className="stat-item__line" style={style}></div>
            <div className="stat-item__indicator" style={style}>{percentage}%</div>
          </div>
        </div>
        <div className="stat-item__btn-container">
          <img src={ButtonIcon} alt="btn" className="stat-item_btn" />
        </div>
      </div>
    );
  }
}



