import React, { Component } from 'react'
import StatItem from '../StatItem'
import Navbar from './Navbar'
import './index.css'

const salesmen = [
  {
    name: 'Andiy Kozak',
    percentage: 100
  },
  {
    name: 'Andiy Kozak',
    percentage: 74
  },
  {
    name: 'Andiy Kozak',
    percentage: 60
  },
  {
    name: 'Andiy Kozak',
    percentage: 23
  },
  {
    name: 'Andiy Kozak',
    percentage: 0
  }
]

export default class Topbar extends Component {
  render() {
    return (
      <div className="main-container__outer">
        <div className="main-container__inner">
          <Navbar />
          <div className="salesmen-list">
            {salesmen.map((person, key) => <StatItem person={person} key={key}/>)}
          </div>
        </div>
      </div>
    );
  }
}



